@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800&display=swap);
:root {
  --mainBackground: hsl(222, 26%, 31%);
  --toggleBackground: hsl(223, 31%, 20%);
  --screenBackground: hsl(224, 36%, 15%);
  --keyBackgroundBlue: hsl(225, 21%, 49%);
  --keyShadowBlue: hsl(224, 28%, 35%);
  --keyBackgroundRed: hsl(6, 63%, 50%);
  --keyShadowRed: hsl(6, 70%, 34%);
  --lightKeyBackground: hsl(30, 25%, 89%);
  --grayishKeyShadow: hsl(28, 16%, 65%);
  --textDark: hsl(221, 14%, 31%);
  --text: hsl(0, 0, 100%);
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(222, 26%, 31%);
  background-color: var(--mainBackground);
  text-align: center;
  font-family: Spartan;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  padding: 100px;
}

input {
  color: white;

  min-width: 600px;
  max-width: 600px;
  padding: 30px;

  background-color: var(--screenBackground);
  border: none;
  outline: none;

  text-align: right;
  font-size: 50px;

  font-family: Spartan;
  font-weight: 700;

  border-radius: 10px;
  margin-bottom: 20px;
}

.white {
  background-color: var(--lightKeyBackground);
  box-shadow: 0 4px 0px 0 var(--grayishKeyShadow);
  color: var(--textDark);
}

.blue {
  background-color: var(--keyBackgroundBlue);
  box-shadow: 0 4px 0px 0 var(--keyShadowBlue);
  color: white;
}

.red {
  background-color: var(--keyBackgroundRed);
  box-shadow: 0 4px 0px 0 var(--keyShadowRed);
  color: white;
}

.Key {
  border: none;
  outline: none;
  padding: 15px 30px;

  font-size: 30px;

  font-family: Spartan;
  font-weight: 700;

  border-radius: 10px;
}

button:active,
button:focus {
  outline: none;
}

.red:active{
  transition: all 0.1s ease 0s;
  box-shadow: 0 3px 0px 0 var(--keyShadowRed);
}
.blue:active{
  transition: all 0.1s ease 0s;
  box-shadow: 0 3px 0px 0 var(--keyShadowBlue);
}
.white:active{
  transition: all 0.1s ease 0s;
  box-shadow: 0 3px 0px 0 var(--grayishKeyShadow);
}

.white-active{
  background-color: hsl(30, 25%, 85%);
}
.ToggleWindow {
  background: var(--toggleBackground);

  min-width: 600px;
  max-width: 600px;
  border-radius: 10px;
  padding: 30px;

  display: block;
  margin: 0 auto;
}

.ToggleWindow div:first-child{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
}
.ToggleWindow div:last-child{
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
}

